<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{shopsinfo.score}}</h1>
          <div class="title">综合评分</div>
          <div class="rank">高于周边商家86%</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title">服务态度</span>
            <Star :ratingcount="shopsinfo.serviceScore" :size="36" />
            <span class="score">{{shopsinfo.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title">商品评分</span>
            <Star :ratingcount="shopsinfo.foodScore" :size="36" />
            <span class="score">{{shopsinfo.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title">送达时间</span>
            <span class="delivery">21分钟</span>
          </div>
        </div>
      </div>
      <div class="split"></div>
      <div class="ratingselect">
        <div class="rating-type border-1px">
          <span
            class="block positive"
            @click="changeSatisfaction(2)"
            :class="{active:isSatisfaction===2}"
          >
            全部
            <span class="count">30</span>
          </span>
          <span
            class="block positive"
            @click="changeSatisfaction(0)"
            :class="{active:isSatisfaction===0}"
          >
            满意
            <span class="count">28</span>
          </span>
          <span
            class="block negative"
            @click="changeSatisfaction(1)"
            :class="{active:isSatisfaction===1}"
          >
            不满意
            <span class="count">2</span>
          </span>
        </div>
        <div class="switch on">
          <span class="iconfont icon-check_circle" @click="changeShowTxte">
            <van-icon name="passed" v-if="onlyTextshow" />
            <van-icon name="circle" v-else />
          </span>
          <span class="text">只看有内容的评价</span>
        </div>
      </div>
      <div class="rating-wrapper">
        <ul class="cr">
          <li v-for="(item,index) in filterContent " :key="index" class="rating-item">
            <div class="avatar">
              <img width="28" height="28" :src="item.avatar" />
            </div>
            <div class="content">
              <h1 class="name">{{item.username}}</h1>
              <div class="star-wrapper">
                <Star :ratingcount="item.score" :size="24" />
                <span class="delivery">{{item.deliveryTime}}</span>
              </div>
              <p class="text">{{item.text}}</p>
              <div class="recommend">
                <span class="iconfont icon-thumb_down">
                  <van-icon color="#ff9900" size="9px" name="good-job-o" />
                </span>
                <span class="item" v-for="(items,index) in item.recommend" :key="index">{{items}}</span>
              </div>
              <div class="time">{{item.rateTime | dateString}}</div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import Star from '../../components/ShopList/stars/star'
import { mapState } from 'vuex'
import { shopsratings } from '../../request/api.js'
import BScroll from '@better-scroll/core'
export default {
  data() {
    return {
      onlyTextshow: false, //是否显示有内容的评论
      ratings: [], //存放评论数据
      isSatisfaction: 2 //满意过滤
    }
  },
  components: {
    Star
  },
  methods: {
    changeSatisfaction(num) {
      this.isSatisfaction = num
      this.$nextTick(() => {
        this.bs.refresh()
      })
    },
    changeShowTxte() {
      this.onlyTextshow = !this.onlyTextshow
      this.$nextTick(() => {
        this.bs.refresh()
      })
    }
  },
  mounted() {
    shopsratings()
      .then(res => {
        this.ratings = res.data
        this.$nextTick(() => {
          this.bs = new BScroll('.ratings', { click: true })
        })
      })
      .catch(err => {
        console.log(err)
      })
  },
  computed: {
    ...mapState(['shopsinfo']),
    filterContent() {
      // 过滤数组
      // 判断条件：onlyTextshow true/false 是否显示有文本的
      // isSatisfaction 0/1/2   满意/不满意/全部
      return this.ratings.filter(item => {
        if (this.onlyTextshow == true) {
          if (this.isSatisfaction == 0) {
            return item.rateType == 0 && item.text.length > 0
          }
          if (this.isSatisfaction == 1) {
            return item.rateType == 1 && item.text.length > 0
          }
          if (this.isSatisfaction == 2) {
            return item.text.length > 0
          }
        } else {
          if (this.isSatisfaction == 0) {
            return item.rateType == 0
          }
          if (this.isSatisfaction == 1) {
            return item.rateType == 1
          }
          if (this.isSatisfaction == 2) {
            return true
          }
        }
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" type="text/stylus">
@import '../../common/stylus/mixins.styl';

.ratings {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background: #fff;
  top: 6vh;
  height: 70vh;

  .overview {
    display: flex;
    padding: 18px 5px;

    .overview-left {
      flex: 0 0 137px;
      padding: 6px 0;
      width: 137px;
      border-right: 1px solid rgba(7, 17, 27, 0.1);
      text-align: center;

      @media only screen and (max-width: 320px) {
        flex: 0 0 120px;
        width: 120px;
      }

      .score {
        margin-bottom: 6px;
        line-height: 28px;
        font-size: 24px;
        color: rgb(255, 153, 0);
        margin-top -2px
      }

      .title {
        margin-bottom: 8px;
        line-height: 12px;
        font-size: 12px;
        color: rgb(7, 17, 27);
      }

      .rank {
        line-height: 10px;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
    }

    .overview-right {
      flex: 1;
      padding: 6px 0 6px 18px;

      @media only screen and (max-width: 320px) {
        padding-left: 6px;
      }

      .score-wrapper {
        margin-bottom: 8px;
        font-size: 0;

        .title {
          display: inline-block;
          line-height: 18px;
          vertical-align: top;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }

        .star {
          display: inline-block;
          margin: 0 12px;
          vertical-align: top;
        }

        .score {
          display: inline-block;
          line-height: 18px;
          vertical-align: top;
          font-size: 12px;
          color: rgb(255, 153, 0);
        }
      }

      .delivery-wrapper {
        font-size: 0;

        .title {
          line-height: 18px;
          font-size: 12px;
          color: rgb(7, 17, 27);
        }

        .delivery {
          margin-left: 12px;
          font-size: 12px;
          color: rgb(147, 153, 159);
        }
      }
    }
  }
}

.split {
  width: 100%;
  height: 16px;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  background: #f3f5f7;
}

.ratingselect {
  .rating-type {
    padding: 18px 0;
    margin: 0 18px;

    .block {
      display: inline-block;
      padding: 8px 12px;
      margin-right: 8px;
      line-height: 16px;
      border-radius: 1px;
      font-size: 12px;
      color: rgb(77, 85, 93);
      background: rgba(77, 85, 93, 0.2);

      &.active {
        background: $green;
        color: #fff;
      }

      .count {
        margin-left: 2px;
        font-size: 8px;
      }
    }
  }

  .switch {
    padding: 12px 18px;
    line-height: 24px;
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    color: rgb(147, 153, 159);
    font-size: 0;

    &.on {
      .icon-check_circle {
        color: $green;
      }
    }

    .icon-check_circle {
      display: inline-block;
      vertical-align: top;
      margin-right: 4px;
      font-size: 24px;
    }

    .text {
      display: inline-block;
      vertical-align: top;
      font-size: 12px;
    }
  }
}

.rating-wrapper {
  padding: 0 18px;
  padding-bottom: 20px;

  .rating-item {
    display: flex;
    padding: 18px 0;
    bottom-border-1px(rgba(7, 17, 27, 0.1));

    .avatar {
      flex: 0 0 28px;
      width: 28px;
      margin-right: 12px;

      img {
        border-radius: 50%;
      }
    }

    .content {
      position: relative;
      flex: 1;

      .name {
        margin-bottom: 4px;
        line-height: 12px;
        font-size: 15px;
        color: rgb(7, 17, 27);
      }

      .star-wrapper {
        margin-bottom: 6px;
        font-size: 0;
        line-height: 10px;

        .star {
          display: inline-block;
          margin-right: 6px;
          vertical-align: top;
        }

        .delivery {
          display: inline-block;
          vertical-align: top;
          line-height: 12px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
      }

      .text {
        margin-bottom: 8px;
        line-height: 18px;
        color: rgb(7, 17, 27);
        font-size: 12px;
      }

      .recommend {
        line-height: 16px;
        font-size: 0;

        .icon-thumb_up, .icon-thumb_down, .item {
          display: inline-block;
          margin: 0 8px 4px 0;
          font-size: 9px;
        }

        .icon-thumb_up {
          color: $yellow;
        }

        .icon-thumb_down {
          color: rgb(147, 153, 159);
        }

        .item {
          padding: 0 6px;
          border: 1px solid rgba(7, 17, 27, 0.1);
          border-radius: 1px;
          color: rgb(147, 153, 159);
          background: #fff;
        }
      }

      .time {
        position: absolute;
        top: 0;
        right: 0;
        line-height: 12px;
        font-size: 10px;
        color: rgb(147, 153, 159);
      }
    }
  }
}
</style>